/* reset */
@font-face {
    font-family: notosans;
    src: url('../asset/font/NotoSansKR-Light.otf');
}
body, html{
    font-family:'notosans',sans-serif;
    margin:0;
    padding:0;
    font-size:15px;
    /* min-width:250px;
    overflow-x: inherit; */
}

p, ul, li{
    margin:0;
    padding:0;
}
a{
    color:#000;
    text-decoration: none;
}
a:active{
    color:#000;
}
.container{
    padding:0px 20px;
    max-width: 700px;
    margin:0px auto;
}
/* 버튼 */
.btn_search{
    padding:10px 20px;
    border-radius: 5px;
    cursor: pointer;
    transition: .1s linear;
    border:1px solid rgb(15, 40, 90);
    background-color: #fff;
    color:rgb(15, 40, 90);
    min-width: 32px;
    display: block;
    text-align: center;
}
.btn_search:hover{
    background-color: rgb(15, 40, 90);
    color:#fff;
}

