.header_section, .header_title{
    background-color: #0f285a;
    color:#fff;
    height:40px;
    display: flex;
    justify-content: space-between;
    padding:0px 20px;
    z-index: 111;
    position:sticky;
    top:0px;
}
.header_section span{
    cursor: pointer;
    font-weight: 700;
    font-size:20px;
    display: flex;
    align-items: center;
    line-height: 2;
    justify-content: center;
}
.header_section span img{
    width:16px;
}