.search_select{
    padding:0 10px;
    max-width: 700px;
    margin:0 auto;
}
.search_result_container{
    height:20vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding:0px 30px;
    text-align: center;
}
.search_result_container span{
    font-size:25px;
}
.search_select_container{
    display: flex;
    flex-direction: column;
    margin-bottom:30px;
}
.search_select_box{
    border-bottom:1px solid #d2d2d2;
    padding:10px 30px;
    display: flex;
    justify-content: space-between;
}