
.apt_detail{
    margin-top:40px;
}
.display_none{
    display: none;
}
.show{
    margin:0 20px;
}
/* .design_title{
    height:4px;
    background-color: rgb(15,40,90);
} */
.img_detail{
   /* height:300px; */
   display: flex;
   justify-content: center;
}
.img_detail_container{
    position: relative;
}
.test_img{
    width:100%;
    max-width: 600px;
}
.click_point{
    position:absolute;
    background-color: #c057f1;
    opacity: 0.6;
    border-radius: 50px;
    padding:7px;
    cursor: pointer;

    top:10%;
    left:20%;
}
.click_point:hover{
    opacity: 1;
}
.click_point:active{
    background-color: rgb(255, 0, 0);
}
.tooltip{
    display: inline-block;
    white-space:nowrap;
    cursor: pointer;
    background-color: #000;
    color:#fff;
    display: block;
    position:absolute;
    margin-top:4%;
    padding:8px 15px;
    border-radius: 5px;
}
.img_detail_like{
    position:absolute;
    top:3%;
    right:3%;
    cursor: pointer;
    z-index: 11;
}
.img_position_box{
    position: relative;
}


.apt_name{
    text-align: center;
    padding: 5px 0;
    background-color: #e7e7e7;
    font-weight: 700;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    position:absolute;
    top:40px; 
    /* top 40px는 -헤더높이 한 것 */
    width:100%;
    left:0;
    z-index: 12;
    cursor: pointer;
}
.side_size {
    font-size: 12px;
    margin: 0 5px;
    background: rgb(15,40,90);
    color: #fff;
    padding: 1px 10px;
    border-radius: 5px;
    display: inline-block;
}
.apt_name_slide{
    display:flex;
    justify-content: space-around;
    width:100%;
    align-items: center;
    transition: 1s linear;

}
.rotate180deg{
    transform:rotate(180deg);
    transition: .3s linear;
}
.rotate0deg{
    transition: .1s linear;
}
.apt_size_box{
    width:100%;
}
.apt_size{
    display: flex;
    flex-wrap: wrap;    
    justify-content: center;
    align-items: center;
    margin: 10px auto;
    max-width:700px;
}
.apt_size span{
    background-color: #fff;
    margin:5px 10px;
    padding:5px 20px;
    cursor: pointer;
    border-radius: 5px;
}
.apt_size_add_info{
    border-top: 1px solid #fff;
    padding: 10px 0;
}
.apt_size_add_info div{
    padding: 5px 0;
}
.download_btn{
    background: #787878;
    padding: 3px 12px;
    color: #fff;
    margin: 3px 10px;
    border-radius: 5px;
    display: inline-block;
}
.apt_size span:hover{
    background-color: #f7f7f7;
}
.apt_info_area{
    padding:0 15px;
    margin:3px 0;
    background-color: #f3f3f3;
    margin-right:10px;
    display: inline-block;
}
.caution_mention{
    font-size:11px;
    color:#787878;
}
.apt_size span.active{
    background-color: rgb(15,40,90);
    color:#fff;
}
/* 사용 자재 리스트 */
.used_materials{
    height:fit-content;
}
.list_in_container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    min-width: 300px;
    margin:10px auto;
    border:1px solid #d2d2d2;
    cursor: pointer;
    position: relative;
}
.list_in_container strong{
    display: contents;
}
.list_in_container:hover{
    background-color: #f7f7f7;
}
.list_in_container2{
    display: flex;
    align-items: center;
    gap:10px;
}
.arrow_box{
    margin-right:20px;
    transition: .3s linear;
}
