.materials_list{
    height:346px;
    overflow-y: scroll;
}
.materials_list {
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
}
.materials_list::-webkit-scrollbar {
    /* Chrome, Safari, Opera*/
    /* display: none;  */
    background-color:rgb(240, 240, 240);
    width: 5px;
}
.materials_list::-webkit-scrollbar-thumb{
    background-color: #0f285a;
}
.materials_img{
    width: 80px;
}
.materials_name{
    font-size:14px;
}
.materials_location span{
    font-size:13px;
}
.materials_info{
    display: flex;
    flex-direction: column;
}
.materials_img_div{
    margin:auto 0;
    display: flex;
    align-items: center;
}
.materials_location, .materials_name{
    display: flex;
    align-items: center;
}
.materials_location img, .materials_name img{
    margin-top:2px;
    margin-right:5px;
}
.no_materials_list{
    border:1px solid rgb(15,40,90);
    border-radius: 3px;
    font-weight:700;
    width:70%;
    margin: 20px auto;
    padding:10px 0;
    text-align: center;
    cursor: pointer;
    padding:25px;
}


.similar_building{
    height:250px;
}
.title{
    text-align: center;
    letter-spacing: -0.5px;
    font-size: 17px;
    padding:5px 0;
    background-color: rgb(15,40,90);
    color:#fff;
    position:sticky;
    top:40px;
    z-index: 110;
}
.similar_img_box{
    display: flex;
    gap:10px;
    padding:10px 15px;
}
.column {
    border-radius: 11px;
    cursor: pointer;
    transition: .1s linear;
    width: 160px !important;
    margin:30px 0;
}
.column:hover{
    background-color: #f7f7f7;
}
.column img{
    width:100%;
    min-width: 150px;
    max-width:200px;
    border-radius: 10px 10px 0 0;
}
.similar_img_box{
    max-width:50vw;
    margin: 0 auto;
}
.similar_info{
    padding:5px 10px;
    border:1px solid #d2d2d2;
    border-radius: 0 0 10px 10px;
}
.similar_info p{
    font-size:13px;
    font-weight: 700;
}
.similar_info span{
    font-size:10px;
}
.slick-slide{
    display: flex !important;
    justify-content: center;
}



/* 아파트 커뮤니티 */
.apt_community{
    width:40vw;
    /* margin:0 auto; */
}
.card{
    box-shadow: 0px 2px 5px #787878;
    padding:10px;
    width:85%;
    margin:15px auto;
    min-width: 280px;
    transition: .2s linear;
}
.card:hover{
    box-shadow: 0px 3px 5px #000;
    transform: translateY(-3px);
}
.commu_title{
    display: flex;
    justify-content: space-between;
    margin-bottom:10px;
}
.commu_sub{
    text-align: center;
    font-size: 15px;
    margin: 10px 0;
}
.commu_title span:first-child, .commu_reply_title span:first-child{
    font-size:13px;
}
.commu_title span:last-child, .commu_reply_title span:last-child{
    margin-left:3px;
    font-size:11px;
}
.commu_img{
    width: 100%;
    height:150px;
    object-fit: cover;
}
.commu_contents{
    word-wrap: break-word;
    border-bottom: 1px solid #d2d2d2;
    padding: 10px 5px;
}
.commu_reply{
    background: #efefef;
    padding: 5px 10px;
    margin:10px 0;
    border-radius: 5px;
}
.commu_reply_input{
    display: flex;
}
.commu_reply_input input{
    width: 68%;
    margin-right:2%;
    height: 24px;
}
.commu_reply_input .submit_btn{
    width:30%;
    text-align: center;
    background-color: #d2d2d2;
    height:30px;
    line-height: 1.8;
    cursor: pointer;
}
.commu_reply_input .submit_btn:hover{
    background-color: rgb(15,40,90);
    color:#fff;
}
.apt_pop_data{
    width:35vw;
}   
.pop_container{
    position: sticky;
    top:90px;
    height: 85vh;
    width: 93%;
    margin:20px 0;
    box-shadow: 0px 2px 5px #000;
    padding:15px;
}
.menu_icon{
    cursor:pointer;
}
/* 아파트 커뮤니티 모바일 쿼리 */
.mo_apt_community{
    width:100%;
}
.mo_pop_container{
    margin:20px auto;
    width:80%;
    margin:20px auto;
    box-shadow: 0px 2px 5px #000;
    padding:15px;
}


/* 미디어쿼어리 */
.web{
    display: flex;
    justify-content: center;
}
.web > div:first-child{
    display:flex;
    flex-direction: column;
    justify-content: center;
}
.web2{
    width:35%;
}
.web3{
    display:flex;
    justify-content: center;
}
.apt_info{
    width:35vw;
}
.similar_building{
    width:50vw;
}
.web_materials_title{
    text-align: center;
    padding: 5px 0;
    font-size: 18px;
}
#table {display: table; width: 90%; margin:30px auto;border-top:1px solid #d2d2d2;}
.row {display: table-row;}
.cell {display: table-cell; padding: 5px 10px; border-bottom: 1px solid #DDD;}
.col1 { width: 30%; background-color: #f5f5f5; font-weight: 700;}
.col2 {width: 70%;}