.search_name{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom:30px;
}
.search_name input{
    padding:11px 21px;
    width: 75%;
    border:1px solid #d2d2d2;
    margin-right: 10px;
    transition: .3s linear;
}
.search_name input:focus{
    outline:none;
    border:1px solid rgb(15,40,90);
}
.search_title{
    text-align: left;
    font-weight: 700;
    margin-bottom:3px;
}

