.search_category{
    margin-bottom:10px;
}
.category{
    margin-bottom:2px;
}
.category span{
    width:20%;
    display: inline-block;
    text-align:left;
    margin-right:5%;
}
.category_box{
    width:70%;
    padding:10px;
    border:none;
    border-bottom:1px solid #d2d2d2;
    transition: .3s linear;
}
.category_box:focus{
    outline: none;
    border-bottom:1px solid rgb(15,40,90);
}
.search_info{
    height:200px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom:30px;
    text-align: center;
    font-size:24px;
}